.Timer-content {
  position: absolute;
  background-color: var(--color-gray);
  top: 0;
  left: 0;
  border-bottom: 1px solid var(--color-gray);
  width: 100%;
}
.progress {
  transition-timing-function: linear;
  width: 0;
  height: 5px;
  transition-duration: 0.1s;
  background-color: red;
}
